form {
  display: flex;
  flex-direction: column;
  width: min(700px, 100%);
}
form #name {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
form input,
form textarea {
  font-size: 15px;
  padding: 10px;
  margin: 5px;
  border-radius: 5px;
  color: var(--textColor);
  background-color: white;
  border: 2px solid black;
}
form ::placeholder {
  color: grey;
}
form input {
  flex: 1;
}
form textarea {
  height: 100px;
  resize: none;
}
form button[type='submit'] {
  border-radius: 10px;
  font-size: 15px;
  padding: 5px 20px;
  margin: auto;
  margin-top: 5px;
  border: 2px solid black;
  background-color: white;
  transition: 0.3s;
}
form button[type='submit']:hover {
  background: lightgrey;
}
